              type: 'solid',
              width: px2px(4)
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: '#326DFF'
                }, {
                  offset: 1,
                  color: '#ffe'
                }]),
                opacity: 0.2
              }
            },
            data: []
          },
          {
            type: 'line',
            name: opt === 0 ? (this.zyqsfxIndex === 0 ? '回款达成' : '回款金额') : (this.zgsqsfxIndex === 0 ? '回款达成' : '回款金额'),
            smooth: true,
            symbol: 'none',
            yAxisIndex: 0,
            lineStyle: {
              type: 'solid',
              width: px2px(4)
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: '#326DFF'
                }, {
                  offset: 1,
                  color: '#ffe'
                }]),
                opacity: 0.2
              }
            },
            data: []
          }
        ]
      }
      const labelList = []
      const xsdcList = []
      const hkList = []
      const nljcList = []
      for (let item of dataArr) {
        labelList.push(item.label)
        xsdcList.push(item.xsdc)
        hkList.push(item.hk)
        nljcList.push(item.nljdc)
      }

      option.xAxis[0].data = labelList
      option.series[0].data = xsdcList
      option.series[1].data = hkList

      return option
    },
    getYzyBarOption (dataArr, opt) {
      let option = {}
      if (opt === 0) {
        if (this.zyqsfxIndex === 0) {
          option = this.getOptionA(option, opt, dataArr)
        } else {
          option = this.getOptionB(option, opt, dataArr)
        }
      } else {
        if (this.zgsqsfxIndex === 0) {
          option = this.getOptionA(option, opt, dataArr)
        } else {
          option = this.getOptionB(option, opt, dataArr)
        }
      }
      return option
    },
    getAllData () {
      this.getZyHkglTableData()
      this.getZyYdjdBarLineData()
      this.getZyQsfxBarLineData()
      this.getZyPmfxTableData()
      this.getZgsHkfxTableData()
    },
    // 直营回款概览数据
    async getZyHkglTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.payback.getZyHkglTableData, {dateStrD: this.dateStr})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        const dataList = []
        for (let item of result.resZy) {
          const obj = {}
          obj.sj = item.period_wid
          if (item.period_wid === '本日') {
            obj.mb = '--'
            obj.xe = formatNumber(initNum(item.re_amt).toFixed(0), 0)
            obj.dc = '--'
            obj.tb = '--'
          } else {
            obj.mb = formatNumber(initNum(item.re_target_amt).toFixed(0), 0)
            obj.xe = formatNumber(initNum(item.re_amt).toFixed(0), 0)
            obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
            obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
          }
          list.push(obj)
        }
        this.pbfxTableData.tableContent = list
        for (let item of result.resZgs) {
          const obj = {}
          obj.sj = item.period_wid
          if (item.period_wid === '本日') {
            obj.mb = '--'
            obj.xe = formatNumber(initNum(item.re_amt).toFixed(0), 0)
            obj.dc = '--'
            obj.tb = '--'
          } else {
            obj.mb = formatNumber(initNum(item.re_target_amt).toFixed(0), 0)
            obj.xe = formatNumber(initNum(item.re_amt).toFixed(0), 0)
            obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
            obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
          }
          dataList.push(obj)
        }
        this.zgshkglTableData.tableContent = dataList
      }
    },
    async getZyYdjdBarLineData (companyName) {
      let params = {
        dateStrD: this.dateStr,
        dateNum: getMonthDayNum(this.dateStr)
      }
      if (companyName === undefined) {
        params.subName = ''
      } else {
        params.subName = companyName
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.payback.getZyYdjdBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        if (companyName === undefined) {
          for (let item of result.resZy) {
            const obj = {}
            obj.label = item.period_wid
            obj.mb = initNum(item.yield_rate) * 100
            obj.sj = initNum(item.target_yield_rate) * 100
            list.push(obj)
          }
          if (list.length > 0) {
            this.zyydjdLineData = list
          } else {
            this.zyydjdLineData = []
          }
        } else {
          for (let item of result.resZgs) {
            const obj = {}
            obj.label = item.period_wid
            obj.mb = initNum(item.yield_rate) * 100
            obj.sj = initNum(item.target_yield_rate) * 100
            list.push(obj)
          }
          if (list.length > 0) {
            this.zgsydjdLineData = list
          } else {
            this.zgsydjdLineData = []
          }
        }
      }
    },
    async getZyQsfxBarLineData (companyName) {
      let params = {
        dateStrM: this.dateStrM
      }
      if (companyName === undefined) {
        params.subName = ''
      } else {
        params.subName = companyName
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.payback.getZyQsfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        if (companyName === undefined) {
          this.zyBarLineDataInit = result.resZy
          this.dealZyBarLineData()
        } else {
          this.zgsBarLineDataInit = result.resZgs
          this.dealZgsBarLineData()
        }
      }
    },
    dealZgsBarLineData () {
      const list = []
      let dataList = []
      dataList = this.zgsBarLineDataInit || []
      if (this.zgsqsfxIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.label = item.period_wid
          obj.xsdc = initNum(item.yield_rate) * 100
          obj.hk = initNum(item.re_yield_rate) * 100
          obj.nljdc = initNum(item.nl_yield_rate) * 100
          list.push(obj)
        }
      } else if (this.zgsqsfxIndex === 1) {
        for (let item of dataList) {
          const obj = {}
          obj.label = item.period_wid
          obj.xsdc = initNum(item.sale_amt)
          obj.hk = initNum(item.re_amt)
          obj.nljdc = initNum(item.nl_yield_rate) * 100
          list.push(obj)
        }
      }

      this.zgsBarLineData = list
    },
    dealZyBarLineData (name) {
      const list = []
      let dataList = []
      dataList = this.zyBarLineDataInit || []
      if (this.zyqsfxIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.label = item.period_wid
          obj.xsdc = initNum(item.yield_rate) * 100
          obj.hk = initNum(item.re_yield_rate) * 100
          obj.nljdc = initNum(item.nl_yield_rate) * 100
          list.push(obj)
        }
      } else if (this.zyqsfxIndex === 1) {
        for (let item of dataList) {
          const obj = {}
          obj.label = item.period_wid
          obj.xsdc = initNum(item.sale_amt)// formatNumber(initNum(item.sale_amt).toFixed(0), 0)
          obj.hk = initNum(item.re_amt)// formatNumber(initNum(item.re_amt).toFixed(0), 0)
          obj.nljdc = initNum(item.nl_yield_rate) * 100
          list.push(obj)
        }
      }
      this.zyBarLineData = list
    },
    async getZyPmfxTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.payback.getZyPmfxTableData, {dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.pmfxTableDataInit = result
        this.dealZyPmfxTableData()
      }
    },
    dealZyPmfxTableData () {
      const list = []
      let dataList = []
      if (this.zypmfxIndex === 0) {
        dataList = this.pmfxTableDataInit.resM || []
      } else if (this.zypmfxIndex === 1) {
        dataList = this.pmfxTableDataInit.resJ || []
      } else if (this.zypmfxIndex === 2) {
        dataList = this.pmfxTableDataInit.resNl || []
      } else if (this.zypmfxIndex === 3) {
        dataList = this.pmfxTableDataInit.resY || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.zgs = item.distribution_name
        obj.mb = formatNumber(initNum(item.re_target_amt).toFixed(0), 0)
        obj.hk = formatNumber(initNum(item.re_amt).toFixed(0), 0)
        obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
        obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
        obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
        list.push(obj)
      }
      this.pmfxTableData = {
        // 这里是为了重置排序，以第一个排序列开始排序
        tableHeader: [{name: '子公司', prop: 'zgs', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '目标', prop: 'mb', needOrder: true, order: '2', isPresent: false, needArrow: false},
          {name: '回款', prop: 'hk', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '达成', prop: 'dc', needOrder: true, order: '0', isPresent: true, needArrow: false},
          {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}],
        tableContent: list.sort(compareSort('mb', false))
      }
    },
    async getZgsHkfxTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.payback.getZgsHkfxTableData, {dateNum: getMonthDayNum(this.dateStr), dateStrD: this.dateStr, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.hkfxTableDataInit = result
        // 要根据选择的月，季，年累，年，进行数据的整理
        this.dealZgsHkfxTableData()
      }
    },
    dealZgsHkfxTableData () {
      const list = []
      let dataList = []
      if (this.zgshkfxIndex === 0) {
        dataList = this.hkfxTableDataInit.resM || []
      } else if (this.zgshkfxIndex === 1) {
        dataList = this.hkfxTableDataInit.resJ || []
      } else if (this.zgshkfxIndex === 2) {
        dataList = this.hkfxTableDataInit.resNl || []
      } else if (this.zgshkfxIndex === 3) {
        dataList = this.hkfxTableDataInit.resY || []
      }

      const headerPc = [
        {name: '子公司', prop: 'zgs', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '目标', prop: 'mb', needOrder: true, order: '2', isPresent: false, needArrow: false},
        {name: '回款', prop: 'hk', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '达成', prop: 'dc', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      ]
      const headerNotPc = this.hkfxTableDataHeaderPc
      if (this.zgshkfxIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.zgs = item.distribution_name
          obj.mb = formatNumber(initNum(item.re_target_amt), 0)
          obj.hk = formatNumber(initNum(item.re_amt), 0)
          obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
          obj.pc = (initNum(item.deviation) * 100).toFixed(1)
          obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
          obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
          list.push(obj)
        }
      } else {
        for (let item of dataList) {
          const obj = {}
          obj.zgs = item.distribution_name
          obj.mb = formatNumber(initNum(item.re_target_amt).toFixed(0), 0)
          obj.hk = formatNumber(initNum(item.re_amt).toFixed(0), 0)
          obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
          obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
          obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
          list.push(obj)
        }
      }
      this.hkfxTableData = {
        tableHeader: this.zgshkfxIndex === 0 ? headerPc : headerNotPc,
        tableContent: list.sort(compareSort('mb', false))
      }

      if (this.hkfxTableData.tableContent.length > 0) {
        this.zgsydjdfxTitle = '月度进度-' + this.hkfxTableData.tableContent[0].zgs
        this.zgsqsfxfxTitle = '趋势分析-' + this.hkfxTableData.tableContent[0].zgs
        this.zgsdppmfxTitle = '店铺排名-' + this.hkfxTableData.tableContent[0].zgs
        this.$eventBus.$emit('resetSelectedRow', {type: 'payback-zgs-hkfx', name: this.hkfxTableData.tableContent[0].zgs})
        this.getZgsDppmTableData(this.hkfxTableData.tableContent[0].zgs)
        // 子公司月度进度
        this.getZyYdjdBarLineData(this.hkfxTableData.tableContent[0].zgs)
        // 子公司趋势分析
        this.getZyQsfxBarLineData(this.hkfxTableData.tableContent[0].zgs)
      } else {
        this.zgsydjdfxTitle = '月度进度'
        this.zgsqsfxfxTitle = '趋势分析'
        this.zgsdppmfxTitle = '店铺排名'
        this.dppmTableData.tableContent = []
        this.zgsydjdLineData = []
        this.zgsBarLineData = []
      }
    },
    async getZgsDppmTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.payback.getZgsDppmTableData, {subName: companyName, dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.dppmTableDataInit = result
        // 要根据选择的日，月，季，年累，年，进行数据的整理
        this.dealDppmTableData()
      }
    },
    dealDppmTableData () {
      const list = []
      let dataList = []
      if (this.zgsdppmIndex === 0) {
        dataList = this.dppmTableDataInit.resM || []
      } else if (this.zgsdppmIndex === 1) {
        dataList = this.dppmTableDataInit.resJ || []
      } else if (this.zgsdppmIndex === 2) {
        dataList = this.dppmTableDataInit.resNl || []
      } else if (this.zgsdppmIndex === 3) {
        dataList = this.dppmTableDataInit.resY || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.dp = item.customer_name
        obj.mb = formatNumber(initNum(item.re_target_amt), 2)
        obj.hk = formatNumber(initNum(item.re_amt), 2)
        obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
        obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
        obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
        list.push(obj)
      }
      this.dppmTableData = {
        // 这里是为了重置排序，以第一个排序列开始排序
        tableHeader: [
          {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '目标', prop: 'mb', needOrder: true, order: '2', isPresent: false, needArrow: false},
          {name: '回款', prop: 'hk', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '达成', prop: 'dc', needOrder: true, order: '0', isPresent: true, needArrow: false},
          {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: list.sort(compareSort('mb', false))
      }
    },
    changeAllType ({btn, index}) {
      if (index === 0) {
        this.showZy = true
        this.zyqsfxIndex = 0
        this.zypmfxIndex = 0
        this.zgsqsfxIndex = 0
        this.dealZyBarLineData()
        this.dealZyPmfxTableData()
      } else {
        this.showZy = false
        this.zyqsfxIndex = 0
        this.zgshkfxIndex = 0
        this.zgsqsfxIndex = 0
        this.zgsdppmIndex = 0
        this.dealZgsHkfxTableData()
        this.dealZgsBarLineData()
        this.dealDppmTableData()
      }
    },
    tabsType ({activeValue, name, type, index}) {
      if (type === 'payback-zy-qsfx') {
        this.zyqsfxIndex = index
        // 通过达成进行数据筛选
        this.dealZyBarLineData()
      } else if (type === 'payback-zy-pmfx') {
        this.zypmfxIndex = index
        this.dealZyPmfxTableData()
      } else if (type === 'payback-zgs-hkfx') {
        this.zgshkfxIndex = index
        this.dealZgsHkfxTableData()
      } else if (type === 'payback-zgs-qsfx') {
        this.zgsqsfxIndex = index
        this.dealZgsBarLineData()
      } else if (type === 'payback-zgs-dppm') {
        this.zgsdppmIndex = index
        this.dealDppmTableData()
      }
    },
    changeTableRow ({row, name, type}) {
      // 当点击了表格的某一行，进行联动操作
      this.zgsydjdfxTitle = '月度进度-' + name
      this.zgsqsfxfxTitle = '趋势分析-' + name
      this.zgsdppmfxTitle = '店铺排名-' + name
      // 子公司店铺排名
      this.getZgsDppmTableData(name)
      // 子公司月度进度
      this.getZyYdjdBarLineData(name)
      // 子公司趋势分析
      this.getZyQsfxBarLineData(name)
    },
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'payback-zy-pmfx') {
        // 这个来分辨哪个表格需要排序
        tempTable = _.cloneDeep(this.pmfxTableData)
      } else if (type === 'payback-zgs-hkfx') {
        tempTable = _.cloneDeep(this.hkfxTableData)
        if (this.zgshkfxIndex === 0) {
          tempTable.tableHeader = _.cloneDeep(this.hkfxTableData.tableHeader)
        } else {
          tempTable.tableHeader = _.cloneDeep(this.hkfxTableDataHeaderPc)
        }
      } else if (type === 'payback-zgs-dppm') {
        tempTable = _.cloneDeep(this.dppmTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'payback-zy-pmfx') {
        // 这个来分辨哪个表格需要排序
        this.pmfxTableData.tableHeader = headerList
        this.pmfxTableData.tableContent = tableContent
      } else if (type === 'payback-zgs-hkfx') {
        this.hkfxTableData.tableHeader = headerList
        this.hkfxTableData.tableContent = tableContent
      } else if (type === 'payback-zgs-dppm') {
        this.dppmTableData.tableHeader = headerList
        this.dppmTableData.tableContent = tableContent
      }
    }
  },
  watch: {
    dateStr: {
      handler () {
        this.getAllData()
      }
    }
  },
  created () {
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.commit('updateHeaderTitle', '回款')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    if (this.dataStr !== '') {
      this.getAllData()
    }
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '总监', URL: '回款'})

    this.$eventBus.$on('userSelected', () => {
      this.getAllData()
    })
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected')
  },
  mounted () {

  }
}
</script>
